import React, { PureComponent } from 'react';
import { getImage } from '~static/js/common';
import './style.scss';

// 
class Item extends PureComponent {
    constructor(props) {
        super(props);
        this.state = {
            commentState: 1, // 0,1,2
            comment: '' // 
        }
    };
    render() {
        const data = this.props.data;
        return (
            <div className="order-list-item">
                <div className="item-img">
                    <img src={getImage(data.img)} alt="{data.title}" />
                </div>
                <div className="item-content">
                    <span>: {data.title}</span>
                    <span>: {data.count}</span>
                    <span>: {data.price}</span>
                </div>
                <div className="item-comment">
                    {
                        this.state.commentState === 0
                            ? <button className="btn" onClick={this.showComment}></button>
                            :
                            this.state.commentState === 1
                                // 
                                ? ''
                                // 
                                : <button className="btn unseleted-btn"></button>
                    }
                </div>
                {
                    this.state.commentState === 1
                        ?
                        <div className="comment-text-container">
                            <textarea
                                placeholder=""
                                onChange={this.textareaChange}
                                value={this.state.comment}
                            >
                            </textarea>
                            <button className="btn" onClick={this.submitHandle}></button>
                            <button className="btn unseleted-btn" onClick={this.hideComment}></button>
                        </div>
                        : ''
                }
            </div>
        );
    };

    componentDidMount() {
        // 
        this.setState({
            commentState: this.props.data.commentState
        });
    };

    // 1
    showComment = () => {
        this.setState({
            commentState: 1
        });
    };

    // 0
    hideComment = () => {
        this.setState({
            commentState: 0,
            comment: ''
        });
    };

    // 
    submitHandle = () => {
        const id = this.props.data.id;
        const submitComment = this.props.submitComment;
        const comment = this.state.comment;
        
         // 
         if (!comment.trim()) {
            return
         }

        // 
        submitComment(id, comment, this.commentOk);
    };

    // 3 
    commentOk = () => {
        this.setState({
            commentState: 2,
            comment: ''
        });
    };

    // textarea
    textareaChange = e => {
        this.setState({
            comment: e.target.value
        });
    };
};

export default Item;



