import React, { PureComponent  } from 'react';
import { connect } from 'react-redux';

import ListComponent from '~components/List';
import LoadMore from '~components/LoadMore';

import { getSearchData } from '~fetch/search/search';

// state

const initialState =  {
    data: [], // 
    hasMore: false, // true
    isLoadingMore: false, // true
    page: 0 // 0
};

class SearchList extends PureComponent {
    constructor(props) {
        super(props);
        this.state = initialState;
    };

    render() {
        return (
            <div>
               {
                    this.state.data.length
                    ? <ListComponent data={this.state.data} />
                    : <div>loading...</div>
                }
                {
                    this.state.hasMore
                    ? <LoadMore isLoadingMore={this.state.isLoadingMore} loadMoreFn={this.loadMoreData} />
                    : ''
                }
            </div>
        );
    };

    // componentDidMount
    // componentDidMountcomponentDidUpdate
    
    // 
    componentDidMount() {
        // 
        this.loadFirstPageData();
    };

    //  componentDidUpdate
    componentDidUpdate(prevProps, prevState) {
        const { keyword, category } = this.props;
        
        // 
        if (keyword === prevProps.keyword && category === prevProps.category) {
            return;
        };

        //  state
        this.setState(initialState);

        // 
        this.loadFirstPageData();
    }

    // 
    loadFirstPageData = () => {
        const cityName = this.props.userinfo.cityName;
        const { keyword, category } = this.props;
        const result = getSearchData(0, cityName, category, keyword);

        this.resultHandle(result);
    };

    // 
    resultHandle = result => {
        result.then(res => {
            return res.json();
        }).then(json => {
            const hasMore = json.hasMore;
            const data = json.data;

            // 
            this.setState({
                hasMore: hasMore,
                data: this.state.data.concat(data)  // n+1n
            });
        });
    };

    // 
    loadMoreData = () => {
        // 
        this.setState({
            isLoadingMore: true
        });

        const cityName = this.props.userinfo.cityName;
        const page = this.state.page;
        const { keyword, category } = this.props;
        const result = getSearchData(page, cityName, category, keyword);

        this.resultHandle(result);
        
        // page
        setTimeout(() =>{
            this.setState({
                page: page + 1,
                isLoadingMore: false
            });
        });
        
    };
}

// ==========================redux react ==========================

const mapStateToProps = state => {
    return {
        userinfo: state.userinfo
    };
};

const mapDispatchToProps = dispatch => {
    return {
    };
};



export default connect(
    mapStateToProps,
    mapDispatchToProps
)(SearchList);



